body {
  margin: 0;
  padding: 0;
  /* font-family: sans-serif; */
  text-align: center;
  font-family: 'Muli', sans-serif;
  background: #202020;
}


ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6 {
  letter-spacing: 3px;
  margin: 0;
}
h3 {
  padding: 10px 25px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

p {
  /* font-size: 110%; */
  letter-spacing: 2px;
  line-height: 30px;
}

textarea {
  font-family: 'Muli', sans-serif;
}

.btn {
  color: white;
  font-size: 14px;
  letter-spacing: 3px;
  outline: none;
  border: 2px solid #fff;
  border-radius: 5px;
  padding: 10px 20px;
  background: transparent;
  cursor: pointer;
}

.panel {
  max-width: 800px;
  margin: 0 auto;
}


/*  Section heading animations */

.section-name {
  display: -ms-flexbox;
  display: flex;
  margin: 0 0 50px 0;
}
.section-name .bar {
  padding: 0 1px;
  background: #64ff8f;
}
.section-name .open-anim {
  overflow: hidden;
  -webkit-animation: 'open';
          animation: 'open'; }
@-webkit-keyframes open {
  0% { max-width: 0; }
  100% { max-width: 100%; }
}
@keyframes open {
  0% { max-width: 0; }
  100% { max-width: 100%; }
}
.section-name .close-anim {
  overflow: hidden;
  -webkit-animation: 'close';
          animation: 'close'; }
@-webkit-keyframes close {
  0% { max-width: 100%; }
  100% { max-width: 0; }
}
@keyframes close {
  0% { max-width: 100%; }
  100% { max-width: 0; }
}
.section-name .fade-in-anim {
  display: -ms-flexbox;
  display: flex;
  -webkit-animation: 'fade-in-anim';
          animation: 'fade-in-anim'; }
@-webkit-keyframes fade-in-anim {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
@keyframes fade-in-anim {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
.section-name .fade-out-anim {
  display: -ms-flexbox;
  display: flex;
  -webkit-animation: 'fade-out-anim';
          animation: 'fade-out-anim'; }
@-webkit-keyframes fade-out-anim {
  0% { opacity: 1; }
  100% { opacity: 0; }
}
@keyframes fade-out-anim {
  0% { opacity: 1; }
  100% { opacity: 0; }
}


