.portfolio {
    color: #fff;
    /* background: #202020; */
    background-image: -webkit-gradient(linear, left bottom, left top, from(#42b0b0), to(#202020));
    background-image: -webkit-linear-gradient(bottom, #42b0b0, #202020);
    background-image: -o-linear-gradient(bottom, #42b0b0, #202020);
    background-image: linear-gradient(0deg, #42b0b0, #202020)
    /* border: 1px dashed gray; */
  }
    
  .portfolio > .container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 80px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* background: rgb(209, 255, 232); */
  }

  .portfolio > .container h4 {
    margin: 0 0 25px 0;
  }
  
  .portfolio > .container > ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  
  .portfolio > .container > ul > li {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 30px;
    /* background: pink; */
  }
  .portfolio > .container .img {
    width: 150px;
    height: 150px;
    /* border: 1px dashed #fff; */
  }
  .portfolio > .container .project-1 .img {
    background: url('../../assets/images/creation_basin.jpg');
    background-size: contain;
  }
  .portfolio > .container .project-2 .img {
    background: url('../../assets/images/woof_walks.jpg');
    background-size: contain;
  }


