.contact {
    color: white;
    background-color: #202020;
}

.contact > .container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 80px 50px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* background: rgb(209, 255, 232); */
}

.contact form {
    width: 100%;
    font-size: 14px;
    letter-spacing: 3px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: left;
}

.contact form > input,textarea {
    outline-color: #21b2a6;
    resize: none;
    color: #fff;
    font-size: 15px;
    width: 100%;
    border: none;
    border-radius: 3px;
    margin: 8px 0 16px 0;
    padding: 8px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #383838;
}

.contact .submit-btn {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 35px 0 0 0;
}
.contact .submit-btn .btn:hover {
    background: #383838;
}

